$logo-big: 80px;
$logo: 40px;
$big-padding: 100px;
$collapse: 700px;
$sticky: true;

header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  box-shadow: var(--shadow);

  @if $sticky {
    position: sticky !important;
    top: 0;
    z-index: 10 !important;
  }
}

#header-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Ensure the canvas is behind other header content */
}


.header-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('../images/bg.svg') no-repeat center center;
  background-size: cover;
  z-index: -1;
  transition: transform 0.1s ease-out;
}




header a {
  color: var(--text);
  text-decoration: none;
}

.home {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.logo {
  height: $logo;
}

.logo > * {
  width: unset;
  height: 100%;
}

.title-text {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  flex-wrap: wrap;
  gap: 5px;
  min-width: 0;
  font-family: var(--title);
  text-align: left;
}

.title {
  font-size: var(--large);
}

.subtitle {
  opacity: 0.65;
  font-weight: var(--thin);
}

.nav-toggle {
  display: none;
  position: relative;
  width: 30px;
  height: 30px;
  margin: 0;
  color: var(--text);
  appearance: none;
  transition: background var(--transition);
}

.nav-toggle:after {
  content: "\f0c9";
  position: absolute;
  left: 50%;
  top: 50%;
  color: var(--text);
  font-size: 15px;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  transform: translate(-50%, -50%);
}

.nav-toggle:checked:after {
  content: "\f00d";
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  /* 增加导航项之间的间隔 */
  gap: 20px; /* 从 10px 增加到 20px */
  font-family: var(--heading);
  text-transform: uppercase;
}

nav > a {
  padding: 5px;
  /* 增加导航项字体大小 */
  font-size: 1.1em; /* 增加字体大小 */
}

nav > a:hover {
  color: var(--primary);
}

header:not([data-big]) {
  @media (max-width: $collapse) {
    justify-content: flex-end;

    .nav-toggle {
      display: flex;
    }

    .nav-toggle:not(:checked) + nav {
      display: none;
    }

    nav {
      align-items: flex-end;
      flex-direction: column;
      width: 100%;
      /* 手机模式下间隔 */
      gap: 15px; /* 可以调整这个值 */
    }
  }
}

header[data-big] {
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: $big-padding 20px;

  @if $sticky {
    top: unset;
  }

  .home {
    flex-direction: column;
    flex-grow: 0;
  }

  .logo {
    height: $logo-big;
    flex-shrink: 0;
  }

  .title-text {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .title {
    font-size: var(--xxl);
  }

  .subtitle {
    font-size: var(--large);
  }
}


/* 语言切换器容器 */
.language-switcher {
  position: static;
  transform: none;
  padding: 0;
  background: none;
  backdrop-filter: none;
  box-shadow: none;
  z-index: auto;
  
  display: flex;
  align-items: center;
  
  /* 增加语言切换器与旁边导航项的距离 */
  margin-left: 40px; /* 从 20px 增加到 40px，使其更远 */
}

/* 语言切换按钮的基础样式 */
.language-switcher .lang-button {
  display: inline-block;
  padding: 8px 12px;
  border: none; /* <-- 修改点：移除边框 */
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.2); /* 柔和的半透明背景 */
  color: #f0f0f0; /* 柔和的文本颜色 */
  text-align: center;
  text-decoration: none;
  font-weight: normal;
  font-size: 0.95em;
  transition: all 0.3s ease;
  cursor: pointer;
  white-space: nowrap;

  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    /* 移除悬停时的边框颜色变化 */
    // border-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transform: translateY(-1px);
  }

  &:active {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(0);
    box-shadow: none;
  }
}